<template>
    <div>
        <div class="hero-body">
            <section class="container">
                <div class="columns">
                    <div class="column">
                        <h1 class="title">Tech Stack</h1>            
                        <br>
                        <div class="columns" v-for="items in chunckPorto" :key="items.id">
                            <div class="column is-2" v-for="item in items" :key="item.id">
                                <img class="img_gray" :src="item.fields.gambar" 
                                    :title="item.fields.judul"
                                    :alt="item.fields.judul">
                            </div> 
                        </div>
                    </div>
                    <div class="column">
                        <h1 class="title">Other Tools</h1>            
                        <br>
                        <div class="columns" v-for="items in chunckTool" :key="items.id">
                            <div class="column is-2" v-for="item in items" :key="item.id">
                                <img class="img_gray" :src="item.fields.gambar" 
                                    :title="item.fields.judul"
                                    :alt="item.fields.judul">
                            </div> 
                        </div>
                    </div>
                </div>
            </section>
        </div>
    </div>
</template>

<style lang="css" scoped>
    .img_gray {
        filter: grayscale(1);
        transition: all 0.5s ease;
    }
    .img_gray:hover {
        filter: grayscale(0);
    }
</style>

<script>
import { AirtableAPI } from '../services/airtable-api';
const airAPI = new AirtableAPI();
let chunk = require('chunk');

export default {
    name: "TechStack",
    data() {
        return {
            selected: undefined,
            items: [],
            tools: [],
        }
    },
    methods: {
        async getData() {
            await airAPI.getAll('Tech').then((data) => {
                this.items = data;
            })
            await airAPI.getAll('Tools').then((data) => {
                this.tools = data;
            })
        },
    },
    computed: {
        chunckPorto() {
            return chunk(this.items, 5)
        },
        chunckTool() {
            return chunk(this.tools, 5)
        }
    },
    mounted() {
        this.getData();
    }
}
</script>
